import React from 'react';
import PropTypes from 'prop-types';
import { Modal } from '@horizin/design-system-organisms';
const styles = {
  icon: {
    borderRadius: 8,
    m: 3,
    maxWidth: 32
  },
  button: {},
  popup: {
    card: true,
    height: 700,
    width: 700
  },
  iframe: {
    height: 700,
    width: 700
  }
};
export const BountyItem = props => (
  <Atom.Box card sx={props.sx}>
    <Atom.Image sx={styles.icon} src={props.src} />
    <Atom.Heading lg thin>
      {props.title}
    </Atom.Heading>
    <Atom.Heading md heavy>
      Earn: ${props.usd_price}
    </Atom.Heading>
    <Atom.Heading sm thin>
      <a target="_blank" href="https://explorer.bounties.network">
        <Atom.Span tag>{props.platform}</Atom.Span>
      </a>
    </Atom.Heading>
    <Atom.Heading sm thin>
      Bounty Stage: {props.bounty_stage}
    </Atom.Heading>
    <Atom.Heading sm thin>
      Create: {props.created}
    </Atom.Heading>
    <Atom.Heading lg normal>
      {/* {props.description} */}
    </Atom.Heading>
    <Modal content={<PreviewPopup />}>
      <Atom.Button sx={styles.button}>Complete Bounty</Atom.Button>
    </Modal>
  </Atom.Box>
);

BountyItem.defaultProps = {};

BountyItem.propTypes = {
  application_count: 0
};

const calculateLink = (platform, id) =>
  ({
    'bounties-network': `https://explorer.bounties.network/bounty/${id}`,
    default: `https://explorer.bounties.network/bounty/${id}`
  }[platform || 'default']);

const PreviewPopup = bounty_id => {
  return (
    <Atom.Box sx={styles.popup}>
      <iframe
        style={styles.iframe}
        src="https://explorer.bounties.network/bounty/3653"
      />
    </Atom.Box>
  );
};

