import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { ThemeProvider } from 'styled-components';
import ChatBot from 'react-simple-chatbot';
import './styles.css';

import { Login } from '3box-ui-system';

// all available props
const theme = {
  background: '#f5f8fb',
  fontFamily: 'Helvetica Neue',
  headerBgColor: '#0886c4',
  headerFontColor: '#fff',
  headerFontSize: '15px',
  botBubbleColor: '#0886c4',
  botFontColor: '#fff',
  userBubbleColor: '#fff',
  userFontColor: '#4a4a4a'
};

class Review extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: '',
      gender: '',
      age: ''
    };
  }

  componentWillMount() {
    const { steps } = this.props;
    const { name, gender, age } = steps;

    this.setState({ name, gender, age });
  }

  render() {
    const { name, gender, age } = this.state;
    return (
      <div style={{ width: '100%' }}>
        <h3>Summary</h3>
        <table>
          <tbody>
            <tr>
              <td>Name</td>
              <td>{name.value}</td>
            </tr>
            <tr>
              <td>Gender</td>
              <td>{gender.value}</td>
            </tr>
            <tr>
              <td>Age</td>
              <td>{age.value}</td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}

Review.propTypes = {
  steps: PropTypes.object
};

Review.defaultProps = {
  steps: undefined
};

class SimpleForm extends Component {
  render() {
    return (
      <ThemeProvider theme={theme}>
        <ChatBot
          botAvatar="data:image/jpeg;base64,"
          steps={[
            {
              id: '1',
              message: 'What is your name?',
              trigger: 'name'
            },
            {
              id: 'name',
              user: true,
              trigger: '3'
            },
            // {
            //   id: 'login',
            //   trigger: '3',
            //   component: <Login display="avatar" />
            // },
            {
              id: '3',
              message: 'Hi {previousValue}! How long have you been coding?',
              trigger: 'years'
            },
            {
              id: 'years',
              options: [
                { value: '1', label: '1 Year', trigger: 'tech' },
                { value: '2', label: '2 Years', trigger: 'tech' },
                { value: '3', label: '3 Years', trigger: 'tech' }
              ]
            },
            {
              id: 'tech',
              message: 'What is your favorite blockchain project?',
              trigger: 'age'
            },
            {
              id: 'age',
              user: true,
              trigger: '7',
              validator: value => {
                if (isNaN(value)) {
                  return 'value must be a number';
                } else if (value < 0) {
                  return 'value must be positive';
                } else if (value > 120) {
                  return `${value}? Come on!`;
                }

                return true;
              }
            },
            {
              id: '7',
              message: 'Great! Check out your summary',
              trigger: 'review'
            },
            {
              id: 'review',
              component: <Review />,
              asMessage: true,
              trigger: 'update'
            },
            {
              id: 'update',
              message: 'Would you like to update some field?',
              trigger: 'update-question'
            },
            {
              id: 'update-question',
              options: [
                { value: 'yes', label: 'Yes', trigger: 'update-yes' },
                { value: 'no', label: 'No', trigger: 'end-message' }
              ]
            },
            {
              id: 'update-yes',
              message: 'What field would you like to update?',
              trigger: 'update-fields'
            },
            {
              id: 'update-fields',
              options: [
                { value: 'name', label: 'Name', trigger: 'update-name' },
                { value: 'gender', label: 'Gender', trigger: 'update-gender' },
                { value: 'age', label: 'Age', trigger: 'update-age' }
              ]
            },
            {
              id: 'update-name',
              update: 'name',
              trigger: '7'
            },
            {
              id: 'update-gender',
              update: 'gender',
              trigger: '7'
            },
            {
              id: 'update-age',
              update: 'age',
              trigger: '7'
            },
            {
              id: 'end-message',
              message: 'Thanks! Your data was submitted successfully!',
              end: true
            }
          ]}
        />
      </ThemeProvider>
    );
  }
}

export default SimpleForm;
